@import "../../../style/variables";

.details-box {
  width: 260px;
  height: calc(100% - 40px);
  display: flex;
  flex-direction: column;
  position: relative;
  margin: 20px;

  .ant-checkbox-wrapper {
    margin: 0 9px 0 3px;
  }

  .categorybox-holder {
    flex-basis: 100%;
    align-self: flex-start;
    height: 100%;
    width: 100%;
    overflow: hidden;
  
    .selector-title {
      font-weight: normal !important;
      white-space: nowrap;
      border: solid $alto;
      border-width: 1px 1px 0;
      padding: 12px 9px 6px;
      margin: 0!important;  
    }
  
    .categorybox-content {
      border: solid $alto;
      border-width: 0px 1px 1px;
      padding: 0 6px;
      height: calc(100% - 38px);
      overflow-y: auto;
    }
  
    .main-list {
      list-style: none;
      margin: 0;
      padding: 0;
      white-space: nowrap;
    }
  
    li {
      line-height: 14px;
      padding: 6px 3px;
    }
  
    svg {
      height: 14px;
      min-height: 14px;
      width: 14px;
      min-width: 14px;
      margin-right: 8px;
    }
  
    span {
      padding: 0;
      font-size: 12px;
    }
  }  
}

.box-title-container {
  white-space: nowrap;
  border: solid $alto;
  border-width: 1px 1px 0;
  padding: 6px 9px;
  margin: 0 0 0 0!important;

  span {
    padding: 0!important;

    button {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 220px;
      text-align: left;
    }
  }
}

.box-title-container {
  height: 100%;
  max-height: 30px;
  line-height: 18px;
  padding: 6px 9px;

  span {
    font-size: 13px;
  }
}

.arrow-holder {
  background: #fff;
  z-index: 1;
  border: 1px solid $alto;
  border-radius: 4px;
  height: 18px;
  width: 42px;
  position: relative;
  margin: -9px 0 -10px;
  align-self: center;

  button {
    margin: 0;
    min-width: 20px !important;
    height: 16px;
    font-size: 20px;
    font-weight: 300;
    cursor: pointer;
    padding: 0;
    line-height: 0;
    transform: rotate(90deg);
  }
  
  button,
  button:focus,
  button.focus-visible,
  button:hover {
    box-shadow: none;
    transition: none;
    background: transparent;
    border: 0;
  }

  span {
    transform: translateY(-1px);
  }  
}
