@import "../../../style/variables";

.dataframes-selection-panel {
  .dataframes-selection-container {
    height: calc(100vh - 180px);
    padding: 10px 20px 0 16px;

    overflow: auto;

    h2 {
      color: $pale-sky;
      font-size: 12px;
      font-weight: bold;
    }

    .dataframes-selection-list {
      list-style: none;
      margin: 0;
      padding: 0 0 0 10px;

      li {
        margin-bottom: 5px;

        .ant-checkbox-wrapper {
          margin-right: 10px;
        }

        .text-to-input-button {
          max-width: 140px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}
